import React from "react";
import Logo from "../../assets/img/header/logo.png";
import MobileIcon from "../../assets/img/header/mobile-menu.svg";
import MobileIconActive from "../../assets/img/header/mobile-menu-active.svg";
import styles from "./index.module.scss";

class Header extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isActiveMenu: false
        }
    }

    onActiveMenu = () => {
        this.setState({
            isActiveMenu: !this.state.isActiveMenu
        })
    };

    render() {
        return (
            <header className={styles.header}>
                <div className={styles.logo_img}>
                    <img src={Logo} alt="" />
                </div>
                <div className={styles.logo_caption}>Free TON Wallet</div>
                <nav className={styles.menu}>
                    <div className={styles.menu_list}>
                        <a href="https://docs.freeton-wallet.org/technical-paper.pdf" target="_blank" rel="noopener noreferrer">Technical Paper (PDF)</a>
                    </div>
                    <div className={styles.menu_list}>
                        <a href="https://docs.freeton-wallet.org/" target="_blank" rel="noopener noreferrer">Documentation</a>
                    </div>
                    <div className={styles.menu_list}>
                        <a href={`${window.location.origin}#find-us`}>Find us</a>
                    </div>
                </nav>
                <div className={styles.mobile_menu}>
                    <img src={this.state.isActiveMenu ? MobileIconActive : MobileIcon} alt="" onClick={this.onActiveMenu} />
                    {this.state.isActiveMenu ? <div className={styles.mobile_block}>
                        <div className={styles.list_block} onClick={this.onActiveMenu}>
                        <a href="https://docs.freeton-wallet.org/technical-paper.pdf" target="_blank" rel="noopener noreferrer">Technical Paper (PDF)</a>
                        </div>
                        <div className={styles.list_block} onClick={this.onActiveMenu}>
                            <a href="https://docs.freeton-wallet.org/" target="_blank" rel="noopener noreferrer">Documentation</a>
                        </div>
                        <div className={styles.list_block}>
                            <a href="#find-us" onClick={this.onActiveMenu}>Find us</a>
                        </div>
                    </div> : null}
                </div>
            </header>
        )
    }
}
export default Header;
